import React from "react";
import { useNavigate } from "react-router-dom";
import { LazyLoadImage } from "react-lazy-load-image-component";
import heart from "../../../assets/heart_icon.png";
import location from "../../../assets/location_icon.png";
import bedroom from "../../../assets/bedroom_icon.png";
import car from "../../../assets/car_icon.png";
import square from "../../../assets/square.png";
import home from "../../../assets/home.png";
import acreage from "../../../assets/acreage.png";
import shower from "../../../assets/shower_icon.png";

const PropertySoldCard = (props) => {
  const navigate = useNavigate();
  return (
    <div
      className={`grid place-items-center grid-cols-1 gap-5 ${props.style} `}
    >
      {props?.PropertyData?.length > 0 &&
        props?.PropertyData?.map((i, index) => (
          <div
            key={index}
            className="flex flex-col bg-white rounded-lg shadow-md hover:shadow-lg cursor-pointer p-2"
            onClick={() => navigate(`/property-house/${i?.id}`)}
          >
            <div className="grid">
              {/* <img src={i?.iconTop} alt="icon" className="rounded-t-lg" /> */}
              <LazyLoadImage
                src={i?.iconTop}
                alt="icon"
                srcSet={i?.iconTop}
                loading="lazy"
                effect="blur"
                className="rounded-t-lg"
              />
              <div className="bg-[#171717] flex justify-between items-center rounded-b-lg p-3 md:p-4">
                {/* <img
                  src={i?.agencyImg}
                  alt="icon"
                  className="w-24 md:w-auto rounded-t-lg mr-2"
                /> */}
                <LazyLoadImage
                  src={i?.agencyImg}
                  alt="icon"
                  srcSet={i?.agencyImg}
                  loading="lazy"
                  effect="blur"
                  className="w-24 md:w-auto rounded-t-lg mr-2"
                />
                <div className="flex justify-center items-center text-white text-xs md:text-base rounded-b-lg">
                  {/* <img
                    src={i?.agentImg}
                    alt="icon"
                    className="w-7 md:w-auto mr-2"
                  /> */}
                  <LazyLoadImage
                    src={i?.agentImg}
                    alt="icon"
                    srcSet={i?.agentImg}
                    loading="lazy"
                    effect="blur"
                    className="w-7 md:w-auto mr-2"
                  />
                  {i?.agentName}
                </div>
              </div>
            </div>
            <div className="mx-4">
              <div className="flex justify-between items-center mt-5 md:mt-6">
                <div className="font-extrabold text-[#404040] text-lg md:text-2xl ">
                  {i?.title}
                </div>
                <div className="bg-[#FFEAEF] rounded-full p-2 md:p-3">
                  <img src={heart} alt="heart" className="w-4 md:w-5" />
                </div>
              </div>

              <div className="flex justify-start items-center mt-2 md:mt-3">
                <img
                  src={location}
                  alt="location"
                  className="w-4 md:w-5 mr-2"
                />
                <div className="font-semibold text-[#737373] text-xs md:text-base">
                  {i?.address}
                </div>
              </div>

              <div className="flex flex-wrap justify-start items-center gap-2 my-5">
                {i?.bedroomCount && (
                  <div className="flex justify-center items-center bg-[#F5F5F5] rounded-md p-2">
                    <img
                      src={bedroom}
                      alt="location"
                      className="w-4 md:w-5 mr-3"
                    />
                    <div className="font-semibold text-[#737373] text-xs md:text-base">
                      {i?.bedroomCount}
                    </div>
                  </div>
                )}
                {i?.showerCount && (
                  <div className="flex justify-center items-center bg-[#F5F5F5] rounded-md p-2">
                    <img
                      src={shower}
                      alt="bedroom"
                      className="w-4 md:w-5 mr-3"
                    />
                    <div className="font-semibold text-[#404040] text-xs md:text-sm">
                      {i?.showerCount}
                    </div>
                  </div>
                )}
                {i?.carCount && (
                  <div className="flex justify-center items-center bg-[#F5F5F5] rounded-md p-2">
                    <img src={car} alt="shower" className="w-4 md:w-5 mr-3" />
                    <div className="font-semibold text-[#404040] text-xs md:text-sm">
                      {i?.carCount}
                    </div>
                  </div>
                )}
                {i?.squareCount && (
                  <div className="flex justify-center items-center bg-[#F5F5F5] rounded-md p-2">
                    <img src={square} alt="car" className="w-4 md:w-5 mr-3" />
                    <div className="font-semibold text-[#404040] text-xs md:text-sm">
                      {i?.squareCount}
                    </div>
                  </div>
                )}
                {i?.appartmentCount && (
                  <div className="flex justify-center items-center bg-[#F5F5F5] rounded-md p-2">
                    <img src={home} alt="car" className="w-4 md:w-5 mr-3" />
                    <div className="font-semibold text-[#404040] text-xs md:text-sm">
                      {i?.appartmentCount}
                    </div>
                  </div>
                )}
                {i?.acreageCount && (
                  <div className="flex justify-center items-center bg-[#F5F5F5] rounded-md p-2">
                    <img src={acreage} alt="car" className="w-4 md:w-5 mr-3" />
                    <div className="font-semibold text-[#404040] text-xs md:text-sm">
                      {i?.acreageCount}
                    </div>
                  </div>
                )}
              </div>

              <div className="text-start font-medium text-[#525252] text-xs md:text-sm my-3">
                {i?.saleDate}
              </div>
            </div>
          </div>
        ))}
    </div>
  );
};

export default PropertySoldCard;
