import React from "react";
import { useNavigate } from "react-router-dom";
import facebook from "../../assets/facebook.png";
import linkden from "../../assets/in.svg";
import printrest from "../../assets/printrest.svg";
import twter from "../../assets/twter.svg";
import youtube from "../../assets/youtube.svg";

import "./Footer.css";

const SmallFooter = () => {
  const img = [facebook, linkden, printrest, twter, youtube]
  const navi = [
    {
      id: 1,
      name: "Advertise with us",
      link: "https://www.facebook.com/"
    },
    {
      id: 2,
      name: "Contact us",
      link: "https://www.linkedin.com/in/linkden/"
    },
    {
      id: 3,
      name: "Agent admin",
      link: "https://www.printrestaurant.com/"
    },
    {
      id: 4,
      name: "Media sales",
      link: "https://www.twitter.com/"
    },
    {
      id: 5,
      name: "Legal",
      link: "https://www.youtube.com/"
    },
    {
      id: 6,
      name: "Privacy",
      link: "https://www.instagram.com/"
    },
    {
      id: 7,
      name: "Site map",
      link: "https://www.instagram.com/"
    },
    {
      id: 8,
      name: "Careers",
      link: "https://www.instagram.com/"
    }
  ]
  const navigate = useNavigate();
  return (
    <>
      <div className=" bg-white ">
        <div className="mx-auto p-5 ">
          <h1 className="w-[120px] bg-[#FFCCD3] rounded-lg text-[#FF444D] font-extrabold text-lg md:text-xl px-5 py-2 cursor-pointer lg:mx-auto mx-0"> La Rosa </h1>
          <h2 className="lg:text-center my-4 font-bold xl:text-lg md:text-base text-sm">realestate.com.au</h2>
          <p className="lg:text-center text-start text-[#737373] xl:text-base md:text-sm text-xs">Lorem ipsum dolor amet, consectetur adipiscing elit. Eget nisl nunc quam ac</p>
          <p className="lg:text-center text-start text-[#737373] xl:text-base md:text-sm text-xs"> sed turpis volutpat. Cursus sed massa non nisi, placerat.</p>


          <div className="flex gap-6 lg:justify-center py-4  ">
            {img?.map((e) => (
              <img className="lg:w-8 w-6 cursor-pointer" src={e} alt="" />
            ))}
          </div>
        </div>
        <div className="  border-t-2 border-gray-200  ">
          <div className="flex flex-wrap md:justify-between py-4 md:px-0 px-5 text-[#404040]" >
            <div className="mx-auto md:text-sm text-xs">Copyright © 2020 realestate.com.au</div>
            <div className="grid md:grid-cols-8 grid-cols-2  xl:pr-12 pr-0 gap-2 md:pt-0 pt-3  md:text-center">
              {navi?.map((data) => (
                <a className="md:text-sm text-xs md:pr-0 pr-8" href={data.link}>{data.name}</a>
              ))}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default SmallFooter;

