import React from "react";
import { LazyLoadImage } from "react-lazy-load-image-component";
import heart from "../../../assets/heart_icon.png";
import location from "../../../assets/location_icon.png";
import bedroom from "../../../assets/bedroom_icon.png";
import car from "../../../assets/car_icon.png";
import shower from "../../../assets/shower_icon.png";
import add_note from "../../../assets/add_note.png";

const SavePropertyCard = (props) => {
  return (
    <div className="grid place-items-center grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-5 my-12">
      {props?.SavePropertyData?.length > 0 &&
        props?.SavePropertyData?.map((i, index) => (
          <div
            key={index}
            className="max-w-lg flex flex-col bg-white rounded-lg shadow-md hover:shadow-lg cursor-pointer p-2"
          >
            <div className="grid place-items-center">
              {/* <img src={i?.iconTop} alt="icon" className="rounded-t-lg" />
              <img src={i?.iconBottom} alt="icon" className="rounded-b-lg" /> */}
              <LazyLoadImage
                src={i?.iconTop}
                alt="icon"
                srcSet={i?.iconTop}
                loading="lazy"
                effect="blur"
                className="rounded-t-lg"
              />
              <LazyLoadImage
                src={i?.iconBottom}
                alt="icon"
                srcSet={i?.iconBottom}
                loading="lazy"
                effect="blur"
                className="rounded-b-lg"
              />
            </div>
            <div className="mx-4">
              <div className="flex justify-between items-center mt-5 md:mt-6">
                <div className="font-extrabold text-[#404040] text-lg md:text-2xl ">
                  {i?.title}
                </div>
                <div className="bg-[#FFEAEF] rounded-full p-2 md:p-3">
                  <img src={heart} alt="heart" className="w-4 md:w-5" />
                </div>
              </div>

              <div className="flex justify-start items-center mt-2 md:mt-3">
                <img
                  src={location}
                  alt="location"
                  className="w-4 md:w-5 mr-2"
                />
                <div className="font-semibold text-[#737373] text-xs md:text-base">
                  {i?.address}
                </div>
              </div>

              <div className="flex justify-start items-center  my-5">
                <div className="flex justify-center items-center bg-[#F5F5F5] rounded-md p-2">
                  <img src={bedroom} alt="bedroom" className="w-5 mr-3" />
                  <div className="font-semibold text-[#737373] text-sm md:text-base">
                    {i?.batrromCount}
                  </div>
                </div>
                <div className="flex justify-center items-center bg-[#F5F5F5] rounded-md p-2 mx-2">
                  <img src={shower} alt="shower" className="w-5 mr-3" />
                  <div className="font-semibold text-[#737373] text-sm md:text-base">
                    {i?.showerCount}
                  </div>
                </div>
                <div className="flex justify-center items-center bg-[#F5F5F5] rounded-md p-2">
                  <img src={car} alt="car" className="w-5 mr-3" />
                  <div className="font-semibold text-[#737373] text-sm md:text-base">
                    {i?.carCount}
                  </div>
                </div>
              </div>

              <div className="w-full border border-t-2 border-[#A3A3A3]" />
              <div className="flex justify-end items-center my-3">
                <div className="font-semibold text-[#525252] text-sm md:text-base mr-2">
                  Add Note
                </div>
                <img src={add_note} alt="add_note" className="w-4 md:w-5" />
              </div>
            </div>
          </div>
        ))}
    </div>
  );
};

export default SavePropertyCard;
